* {
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
}

.hero {
  background-image: linear-gradient(45deg, rgba(255, 74, 115, 0.8), rgba(0, 102, 176, 0.8));
}

.banner {
  background-image: url("./assets/img.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  background-attachment: fixed;
}

.wave {
  background-image: url("./assets/wavybone.png");
  background-repeat: repeat-x;
  margin-bottom: -8px;
}

#navbarMenu {
  transition: left 100ms ease-in, left 500ms ease-out;
}
#dropdownMenu {
  transition: transform 200ms ease-in-out;
}

table,
th,
td {
  border: 1px solid #fff;
}

.applying-h1 {
  height: 50px;
}

.applying-h2 {
  height: 150px;
  text-align: center;
}

.setBg {
  background-image: linear-gradient(to right top, #2a2c2e, #243546, #1a3d60, #0d457a, #0c4b94);
}

.setBg2 {
  background: rgb(2, 33, 77);
  background: linear-gradient(90deg, rgba(2, 33, 77, 1) 100%, rgba(0, 212, 255, 1) 100%);
}
