@tailwind base;
@tailwind components;
@tailwind utilities;

@keyframes defilement-rtl {
  0% {
    -webkit-transform: translate(-100%);
    transform: translate(-100%);
  }
  100% {
    -webkit-transform: translate(100%);
    transform: translate(100%);
  }
}

.defilement {
  animation: defilement-rtl 20s infinite linear;
}

.defilement:hover {
  animation-play-state: paused;
}
